export default {
  props: {
    sortOpen: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      sortData: {
        priceRange: ""
      }
    };
  },
  watch: {
    sortData: {
      handler(newValue) {
        this.$emit('updated', newValue);
      },
      deep: true,
      immediate: true
    },
    $route() {
      this.clearSort();
    }
  },
  methods: {
    clearSort() {
      this.sortData.priceRange = "";
    }
  }
};
