export default {
  data() {
    return {
      gridView: true
    };
  },
  props: {
    searchTerm: {
      type: String,
      default: "",
    },
    totalMatches: {
      type: Number,
      defauly: 0,
    },
    collectionView: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    currentViewImage() {
      return this.gridView ? "/icons/dark/grid.png" : "/icons/dark/single-box.png"
    }
  },
  methods: {
    openFilters() {
      this.$emit("openFilters");
    },
    openSort() {
      this.$emit("openSort");
    },
    toggleGridView() {
      this.gridView = !this.gridView;
      this.$emit('viewChanged', this.gridView);
    }
  }
};
